import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Group, Div, Cell, Avatar, Button, Link} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';


const osname = platform();

const Vg = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="clans">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
		    ViolentGaming
		</PanelHeader>
		<Group>
			<Div>
        <Cell
          photo="https://pp.userapi.com/c849532/v849532071/18ec14/PGKDRYuJlXk.jpg"
          description="Оффициальная группа"
          bottomContent={<Button  component="a" href="https://vk.com/violentgg">Подписаться</Button>}
          before={<Avatar src="https://pp.userapi.com/c849532/v849532071/18ec14/PGKDRYuJlXk.jpg" size={80}/>}
          size="l"
        >
         ViolentGaming
        </Cell>
			</Div>
		</Group>
        <Group title="Контакты">
            <Div>
Vk: <Link href="https://vk.com/violentgg">@violentgg</Link> <br />
Discord: <Link href="https://discord.gg/X3xektS">discord.gg/X3xektS</Link>
            </Div>
        </Group>
<Group title="Достижения">
              <Div>
              🏆 Победители на CPL S3 <br />
🏆 Второе место на MST Pro League <br />
🏆 Финалисты NerdRagePro Tournament <br />
              </Div>
            </Group>
	</Panel>

);

Vg.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Vg;