import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Group, Div, Cell, Avatar, Button, Link, List} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon16FireVerified from '@vkontakte/icons/dist/16/fire_verified'

const osname = platform();

const Csn = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="persik">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
		    CSN <Icon16FireVerified />
		</PanelHeader>
		<Group>
			<Div>
        <Cell
          photo="https://pp.userapi.com/c845122/v845122120/17d3f0/6v0Tj9n8g9A.jpg"
          description="Оффициальная группа"
          bottomContent={<Button  component="a" href="https://vk.com/csn_tournament">Подписаться</Button>}
          before={<Avatar src="https://pp.userapi.com/c845122/v845122120/17d3f0/6v0Tj9n8g9A.jpg" size={80}/>}
          size="l"
        >
        Central eSports Network
        </Cell>
			</Div>
		</Group>
        <Group title="Описание">
            <Div>
            Central eSports Network Tournament - это часть организации, которая проводит турниры по Critical Ops.
            </Div>
        </Group>
<Group title="Группы организации">
<List>
        <Cell component="a" href="https://vk.com/csn_cops" before={<Avatar src="https://pp.userapi.com/c847220/v847220187/1fe3a9/0U6j0RKzTtg.jpg" />}description="Новостное сообщество">Csn | Critical Ops</Cell>
        <Cell component="a" href="https://vk.com/csn_comm" before={<Avatar src="https://pp.userapi.com/c856032/v856032647/218c5/GUjwL7Nzinw.jpg" />}description="Комьюнити">Csn | Community</Cell>
      </List>
            </Group>
            <Group title="Администрация">
        <Cell component="a" href="https://vk.com/deekeydk"
          size="80"
          description="Csn"
          before={<Avatar src="https://pp.userapi.com/c855620/v855620787/51c32/3ZWEtjQQaUo.jpg"/>}
        >
          Daniil Konstantinovich
        </Cell>
      </Group>
	</Panel>

);

Csn.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Csn;