import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Group, Div, List, Cell, Avatar} from '@vkontakte/vkui';
import './Persik.css';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();



const Persik = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="home">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Турниры
		</PanelHeader>
		<Group>
			<Div>
				Список обновляется каждую неделю.
			</Div>
			</Group>
			<Group title="Список турниров." description="Если вы хотите чтобы тут появился и ваш турнир, то пиши нам в личные сообщения.">
        <List>
          <Cell onClick={props.go} data-to="csn" before={<Avatar src="https://pp.userapi.com/c845122/v845122120/17d3f0/6v0Tj9n8g9A.jpg" size={65} />} >Csn | CSN ELITE TOURNAMENT</Cell>
        </List>
      </Group>
	</Panel>
);

Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Persik;
